import React, { useState } from 'react'
import './IndividualLeaderboard.css';
import PropTypes from 'prop-types';
import Top3Users from './IndividualLeaderboardComponents/Top3Users';
import LeaderboardNavigation from './IndividualLeaderboardComponents/LeaderboardNavigation';
import LeaderboardSpecificStats from './IndividualLeaderboardComponents/LeaderboardSpecificStats';
import Leaderboard from './IndividualLeaderboardComponents/Leaderboard';

function IndividualLeaderboard(props) {
    const [currentLeaderboard, setCurrentLeaderboard] = useState(props.location.name);
    let navigationOrder = props.location.navigationOrderUnsorted;
    let index = navigationOrder.indexOf(currentLeaderboard);

    const onNextClick = () => {
        if (index + 1 === navigationOrder.length) {
            index = 0;
        } else {
            index++;
        }
        setCurrentLeaderboard(navigationOrder[index]);
    }

    const onPrevClick = () => {
        if (index - 1 < 0) {
            index = navigationOrder.length-1;
        } else {
            index--;
        }
        setCurrentLeaderboard(navigationOrder[index]);
    }

    return (
        <div className="individual-leaderboard">
            <div className="leaderboard-title-and-navigation">
                <h1>{currentLeaderboard} Leaderboard</h1>
                <LeaderboardNavigation onPrevClick={onPrevClick} onNextClick={onNextClick} />
            </div>
            <div className="leaderboard-spotlight-row">
                <Top3Users />
                <LeaderboardSpecificStats />
            </div>
            <Leaderboard leaderboardTitle={currentLeaderboard}/>
        </div>
    )
}

export default IndividualLeaderboard;

IndividualLeaderboard.propTypes = {
    name: PropTypes.string,
    navigationOrderUnsorted: PropTypes.array
};