import React from 'react'
import "./HomeButtonSmall.css";
import PropTypes from 'prop-types';
import * as AiIcons from 'react-icons/ai';
import { HomeButtonNavButton } from '../../../components/HomeButtonNavButton';

function HomeButtonSmall(props) {
    let mainData, subtitle, path;
    switch(props.title) {
        case("Your Brier Score"):
            mainData = "1.03";
            subtitle = "0 = Highest, 2 = Lowest";
            path = "forecast"
            break;
        case("Your Learn Progress"):
            mainData = "3/7";
            subtitle = "Sections Completed";
            path = "learn"
            break;
        case("Leaderboards"):
            mainData = "14th";
            subtitle = "Global Rank"
            path ="leaderboard-select"
            break;
        default:
            break;
    }
    return (
        <div className="home-button-small">
            <div className="home-button-small-title">
                <h3>{props.title}</h3>
                <div className="home-button-scroll">
                    {/* <AiIcons.AiOutlineCaretLeft size={20} onClick={() => props.handleClick(props.title, "left")}/> */}
                    <AiIcons.AiOutlineCaretRight className="home-button-nav-right" size={20} onClick={() => props.handleClick(props.title, "right")}/>
                </div>
            </div>
            <h1 className="home-button-small-data">{mainData}</h1>
            <h3 className="home-button-small-subtitle">{subtitle}</h3>
            <HomeButtonNavButton path={path} />
        </div>
    )
}

export default HomeButtonSmall;

HomeButtonSmall.propTypes = {
    title: PropTypes.string.isRequired
};