import React from 'react'
import './LeaderboardNavigation.css';
import PropTypes from 'prop-types';
import * as AiIcons from 'react-icons/ai';

function LeaderboardNavigation(props) {
    return (
        <div className="leaderboard-navigation">
            <button className="navigation-button" onClick={props.onPrevClick}>
                <h2>Previous Leaderboard</h2>
                <AiIcons.AiOutlineCaretLeft size={30} />
            </button>
            <button className="navigation-button" onClick={props.onNextClick}>
                <AiIcons.AiOutlineCaretRight size={30} />
                <h2>Next Leaderboard</h2>
            </button>
        </div>
    )
}

export default LeaderboardNavigation;

LeaderboardNavigation.propTypes = {
    onPrevClick: PropTypes.func.isRequired,
    onNextClick: PropTypes.func.isRequired
};