import React from 'react'
import PropTypes from 'prop-types';
import './HomeButtonLarge.css';
import { FakeLeaderboards } from './FakeLeaderboards';
import { HomeButtonNavButton } from '../../../components/HomeButtonNavButton';
import { CanvasJSChart } from 'canvasjs-react-charts';

function HomeButtonLarge(props) {
    let options = {
        animationEnabled: true,
        data: [{
            type: "line",
            indexLabelFontSize: 16,
            dataPoints: [
                { y: 0.30, label: "FC 1", indexLabel: "Best" },
                { y: 0.98, label: "FC 2" },
                { y: 0.65, label: "FC 3" },
                { y: 0.45, label: "FC 4" },
                { y: 1.24, label: "FC 5", indexLabel: "Worst" },
            ]
        }]
    }

    return (
        <div className="home-button-large">
            <h3 className="home-button-large-title">{props.title}</h3>
            { props.title === "Your Recent Stats" && 
                <CanvasJSChart className="home-recent-forecasts-chart" options={options} />
            }
                { props.title === "Your Recent Stats" && <HomeButtonNavButton path="my-profile" /> }

            { props.title === "Leaderboards" && 
                <ul className="home-button-large-leaderboards">
                    {FakeLeaderboards.map((item, index) => {
                        return (
                            <li key={index} className="leaderboard-user">{item.LeaderboardPosition} | {item.LeaderboardUsername} | {item.LeaderboardScore}</li>
                        )
                    })}
                </ul>
            }
            { props.title === "Leaderboards" && <HomeButtonNavButton path="leaderboard" /> }
        </div>
    )
}

export default HomeButtonLarge;


HomeButtonLarge.propTypes = {
    title: PropTypes.string.isRequired
};