import React from 'react';
import './LeaderboardShortcut.css';
import PlaceholderIcon from './leaderboard.png';

function LeaderboardShortcut(props) {
    return (
        <div className="leaderboard-shortcut">
            <img src={PlaceholderIcon} alt=""/>
            <h3>{props.name}</h3>
        </div>
    )
}

export default LeaderboardShortcut;
