import React from 'react'
import './Navbar.css';
import { Link } from 'react-router-dom';
import { NavbarData } from './NavbarData';
import { NavbarProfileStats } from './NavbarProfileStats';
import { IconContext } from 'react-icons';
import FakeProfilePic2 from './ProfileP.png'

function Navbar() {
    return (
        <>
            <IconContext.Provider value ={{ color: "#fff" }}>
                <div className="navbar"></div>
                <nav className="nav-menu">
                    <img className="nav-logo" src={"./sd.png"} alt="Fantasy Forecast Logo"/>
                    <div className="nav-profile-pic">
                        <img src={FakeProfilePic2} alt="User's profile pic" />
                    </div>
                    <ul className="nav-menu-items">
                        {NavbarProfileStats.map((item, index) => {
                            return (
                                <li key={index} className={item.cName}>
                                    {item.userDetail}: {item.userInfo} 
                                </li>
                            )
                        })}
                        {NavbarData.map((item, index) => {
                            return (
                                <li key={index} className={item.cName}>
                                    <Link to={item.path}>
                                        {item.icon}
                                        <span className="nav-title">{item.title}</span>
                                    </Link>
                                </li>
                            )
                        })}
                    </ul>
                </nav>
            </IconContext.Provider>
        </>
    )
}

export default Navbar;