import React from 'react';
import './ProfileSummary.css';
import { FakeProfileSummaryInfo } from './FakeProfileSummaryInfo';

function ProfileSummary() {
    return (
        <div className="profile-summary">
            <ul className="profile-summary-list">
                {FakeProfileSummaryInfo.map((item, index) => {
                    return (
                        <li key={index} className="profile-summary-list-item">
                            <h3>{item.userInfo}:</h3>
                            <h4>{item.userData}</h4>
                        </li>
                    )
                })}
            </ul>
        </div>
    )
}

export default ProfileSummary;