import React from 'react';
import './Topic.css';
import PropTypes from 'prop-types';
import * as AiIcons from 'react-icons/ai';
import { TopicContent } from './TopicContent';

function Topic(props) {
    let youtubeLink;
    let text;

    switch(props.title) {
        case("Fantasy Forecast Points"):
            //youtubeLink = 
            text = [];
            break;
        case("Brier Scores"):
            youtubeLink = <iframe width="560" height="315" src="https://www.youtube.com/embed/0XaBygN-FZ8" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
            text = TopicContent[1];
            break;
        case("The Good Judgment Project"):
            youtubeLink = <iframe width="560" height="315" src="https://www.youtube.com/embed/uJb63rMNrbE" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
            text = TopicContent[2];
            break;
        case("Superforecasters"):
            youtubeLink = <iframe width="560" height="315" src="https://www.youtube.com/embed/wgybyx5cxEc" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
            text = TopicContent[3];
            break;
        default:
            youtubeLink = <iframe width="560" height="315" src="https://www.youtube.com/embed/0XaBygN-FZ8" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
            text = [];
            break;
    }

    return (
        <div className="topic">
            <h1 className="topic-title">{props.title}</h1>
            <div className="topic-youtube-video">{youtubeLink}</div>
            <div className="topic-text">{text}</div>
            <button className="topic-to-quiz-button"><AiIcons.AiOutlineArrowRight size={20} /></button>
        </div>
    )
}

export default Topic;

Topic.propTypes = {
    title: PropTypes.string.isRequired
};