import React from 'react';
import './TopicsMenu.css';
import PropTypes from 'prop-types';
// import Topic from './Topic';

function TopicsMenu(props) {
    return (
        <div className="topics-menu">
            <h2>Topics</h2>
            <ul className="topics-list">
                <li onClick={() => props.handleClick("Fantasy Forecast Points")}><h3>Fantasy Forecast Points</h3></li>
                <li onClick={() => props.handleClick("Brier Scores")}><h3>Brier Scores</h3></li>
                <li onClick={() => props.handleClick("The Good Judgment Project")}><h3>The Good Judgment Project</h3></li>
                <li onClick={() => props.handleClick("Superforecasters")}><h3>Superforecasters</h3></li>
            </ul>
        </div>
    )
}

export default TopicsMenu;


TopicsMenu.propTypes = {
    handleClick: PropTypes.func.isRequired
};