import React, { useState } from 'react';
import './ProfileStats.css';
import { CanvasJSChart } from 'canvasjs-react-charts';

function ProfileStats() {
    //Store this as state in another component (Remember, 1 component = 1 job!)
    //Static data as a placeholder!
    let recentForecastData = [
        { y: 0.30, label: "FC 1"},
        { y: 0.98, label: "FC 2" },
        { y: 0.65, label: "FC 3" },
        { y: 0.45, label: "FC 4" },
        { y: 1.24, label: "FC 5" , indexLabel: "Worst" },
        { y: 0.89, label: "FC 6" },
        { y: 0.56, label: "FC 7" },
        { y: 0.30, label: "FC 8" },
        { y: 0.10, label: "FC 9" , indexLabel: "Best" },
        { y: 0.32, label: "FC 10" }
    ];

    let allForecastData = [
        { y: 1.30, label: "FC 1"},
        { y: 1.98, label: "FC 2" },
        { y: 1.65, label: "FC 3" },
        { y: 1.45, label: "FC 4" },
        { y: 0.24, label: "FC 5" },
        { y: 1.89, label: "FC 6" },
        { y: 1.56, label: "FC 7" },
        { y: 1.30, label: "FC 8" },
        { y: 0.12, label: "FC 9" },
        { y: 1.32, label: "FC 10" },
        { y: 0.30, label: "FC 11"},
        { y: 0.98, label: "FC 12" },
        { y: 0.65, label: "FC 13" },
        { y: 0.45, label: "FC 14" },
        { y: 1.24, label: "FC 15" , indexLabel: "Worst" },
        { y: 0.89, label: "FC 16" },
        { y: 0.56, label: "FC 17" },
        { y: 0.30, label: "FC 18" },
        { y: 0.10, label: "FC 19" , indexLabel: "Best" },
        { y: 0.32, label: "FC 20" }
    ];

    const [stats, setStats] = useState(recentForecastData);

    let recentForecasts = {
        animationEnabled: true,
        title: {
            // text: "My Recent Forecasts"
        },
        data: [{
            type: "line",
            indexLabelFontSize: 16,
            dataPoints: stats
        }]
    }

    return (
        <div className="profile-stats">
            <h1>My Stats</h1>
            <h3>Check out your Fantasy Forecast stats and how you're stacking up to the rest of the playerbase.</h3>
            <div className="profile-stats-inner">
                <div className="profile-stats-recent-forecasts">
                    <br/>
                    <ul className="profile-stats-selectors">
                        <li onClick={() => setStats(recentForecastData)}><h3>Recent Forecasts</h3></li>
                        <h2>|</h2>
                        <li onClick={() => setStats(allForecastData)}><h3>All Forecasts</h3></li>
                    </ul>
                    <CanvasJSChart className="profile-recent-forecasts-chart" options={recentForecasts} />
                </div>
                <div className="profile-stats-grid">
                    <br/>
                    <h3>Comparative Stats</h3>
                    <div className="profile-stats-grid-headers">
                        <h3>Me</h3>
                        <h3>Global</h3>
                    </div>
                    <div className="profile-stats-grid-body">
                        {/* Had to hard-code it as mapping through FakeData */}
                        {/* organised it into columns which I didn't want */}
                        {/* This is a temp solution anyway to be replaced */}
                        {/* by real dynamic data in the future */}
                        <h4 style={{color: "red"}}>0.06</h4>
                        <h3>Best</h3>
                        <h4 style={{color: "green"}}>0.02</h4>
                        <h4 style={{color: "green"}}>1.58</h4>
                        <h3>Worst</h3>
                        <h4 style={{color: "red"}}>2.00</h4>
                        <h4 style={{color: "red"}}>1.03</h4>
                        <h3>Average</h3>
                        <h4 style={{color: "green"}}>0.89</h4>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ProfileStats;
