import React, { useState } from 'react';
import './Home.css';
import HomeHeader from './HomeHeader/HomeHeader';
import HomeNewsFeed from './HomeSectionButtons/HomeNewsFeed';
import HomeButtonSmall from './HomeSectionButtons/HomeButtonSmall';
import HomeButtonLarge from './HomeSectionButtons/HomeButtonLarge';
import NewForecastsCallToAction from './HomeSectionButtons/NewForecastsCallToAction';
import Onboarding from './HomeSectionButtons/Onboarding';

function Home() {
    let width, height;
    width = window.innerWidth;
    height = window.innerHeight;
    
    const [onboardComplete, setOnboardComplete] = useState(false);
    const [buttonText, setButtonText] = useState("Hide");
    const [homeStats, setHomeStats] = useState("Your Brier Score");
    const [subtitle, setSubtitle] = useState("0 = Highest, 2 = Lowest");

    const onboardingButtonClick = (onboardComplete, buttonText) => {
        setOnboardComplete(!onboardComplete);
        buttonText === "Hide" ? setButtonText("Show") : setButtonText("Hide");
    }

    const scrollHomeStats = (currentStatsShowing, clickDirection) => {
        switch(clickDirection) {
            case("right"):
                switch(currentStatsShowing) {
                    case("Your Brier Score"):
                        setHomeStats("Your Learn Progress");
                        setSubtitle("Sections Completed");
                        break;
                    case("Your Learn Progress"):
                        setHomeStats("Leaderboards");
                        setSubtitle("Global Rank");
                        break;
                    case("Leaderboards"):
                        setHomeStats("Your Brier Score");
                        setSubtitle("0 = Highest, 2 = Lowest");
                        break;
                    default:
                        setHomeStats("Your Brier Score");
                        setSubtitle("0 = Highest, 2 = Lowest");
                        break;
                }
        }
    }

    return (
        <div className="home">
            <HomeHeader />
            <h3>{width}</h3>
            <h3>{height}</h3>
            <div className="home-page-div">
                <div className="onboarding-div">
                    <Onboarding handleClick={() => onboardingButtonClick(onboardComplete, buttonText)} buttonText={buttonText} isComplete={onboardComplete} />
                </div>
                <div className="home-page-grid">
                    <div className="home-page-news-feed">
                        <HomeNewsFeed />
                    </div>
                    <div className="home-page-stats-div">
                        <NewForecastsCallToAction /> 
                        <HomeButtonSmall title={homeStats} subtitle={subtitle} handleClick={scrollHomeStats} />
                        <HomeButtonLarge title="Your Recent Stats" /> 
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Home;
