import React from 'react'
import './HomeHeader.css';

function HomeHeader() {
    return (
        <div className="home-header-intro">
            <h1>Welcome back, User!</h1>
            <p>This the home page for Fantasy Forecast. Use this as a central hub for 
                navigating the site! Check out your forecasting stats, where you sit on
                the leaderboards, or maybe top up your knowledge about the current election,
                your predictive abilities, and more!
            </p>
        </div>
    )
}

export default HomeHeader
