import React from 'react'
import './NewForecastsCallToAction.css';
import { HomeButtonNavButton } from '../../../components/HomeButtonNavButton';

function NewForecastsCallToAction() {
    return (
        <div className="new-forecasts-container">
            <h1 className="new-forecasts-title">New Forecasts</h1>
            {/* Add a check, if no new forecasts, display the second <p></p> */}
            <p>Since you were last online, some new problems have dropped!</p>
            {/* <p>No new problems have dropped since you were last online.</p> */}
            <br/>
            <p>"On Friday 19th March, the UK will record over 5,000 new cases of COVID-19."</p>
            <br/>
            <p>"On Friday 19th March, the UK will record at least 60 deaths from COVID-19."</p>
            <br/>
            <p>"Chelsea and Atletico Madrid will draw in their second leg game."</p>
            <HomeButtonNavButton path="forecast" />
        </div>
    )
}

export default NewForecastsCallToAction
