import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './LeaderboardGrid.css';
import PropTypes from 'prop-types';
import LeaderboardShortcut from './LeaderboardShortcut';
import { FakeLeaderboardTopGrid } from './FakeLeaderboardTopGrid';
import { FakeLeaderboardBottomGrid } from './FakeLeaderboardBottomGrid';

function LeaderboardGrid(props) {
    const [yourMarketNames, setYourMarketNames] = useState([]);
    const [allMarketsNames, setAllMarketNames] = useState([]);

    useEffect(() => {
        const yourMarkets = Array.from(document.getElementsByClassName("your-markets-leaderboards"));
        let leaderboardNames = yourMarkets.map(leaderboard => {
            return leaderboard.innerText;
        });
        setYourMarketNames(leaderboardNames);

        const allMarkets = Array.from(document.getElementsByClassName("all-markets-leaderboards"));
        let allMarketNames = allMarkets.map(leaderboard => {
            return leaderboard.innerText;
        });
        setAllMarketNames(allMarketNames);
    }, [])


    return (
        <div className="leaderboard-grid">
            {props.user === true && FakeLeaderboardTopGrid.map((item, index) => {
                const leaderboardProps = {
                    pathname: "/leaderboard",
                    name: item.leaderboardName,
                    navigationOrderUnsorted: yourMarketNames
                };
                return (
                    <Link 
                        key={index} 
                        to={leaderboardProps} 
                        className="your-markets-leaderboards">
                            <LeaderboardShortcut name={item.leaderboardName} />
                    </Link>
                )
            })}
            {props.user === false && FakeLeaderboardBottomGrid.map((item, index) => {
                const leaderboardProps = {
                    pathname: "/leaderboard",
                    name: item.leaderboardName,
                    navigationOrderUnsorted: allMarketsNames
                };
                return (
                    <Link 
                        key={index} 
                        to={leaderboardProps}
                        className="all-markets-leaderboards">
                            <LeaderboardShortcut name={item.leaderboardName} />
                    </Link>
                )
            })}
        </div>
    )
}

export default LeaderboardGrid;


LeaderboardGrid.propTypes = {
    user: PropTypes.bool.isRequired
};