import React, { useState, useEffect } from 'react';
import './HomeNewsFeed.css';
import ProfilePic from './ProfileP.png';
import * as AiIcons from 'react-icons/ai';
import { FakeNewsFeed } from './FakeNewsFeed';

function HomeNewsFeed() {
    //When the news feed is working, separate the stateful logic from the rendering logic by
    //putting the state logic (hooks and shit) inside the HomeNewsFeedData component and importing
    //it into this one
    const [feed, setFeed] = useState([]);
    const [post, setPost] = useState(false);
    
    useEffect(() => {
        let xml2js = require("xml2js");
        xml2js.parseStringPromise(FakeNewsFeed)
            .then(response => setFeed(response.collection.item))
    }, []);

    return (
        <div className="home-page-news-feed-container">
            <h1 className="news-feed-title">News Feed</h1>
            <div className="post">
                {post === false && <button className="create-post-button" onClick={() => setPost(true)}>Post to your feed</button>}
                {post === true && 
                    <form className="create-post-form">
                        <fieldset className="create-post-fieldset">
                            <legend>Post to your feed</legend>
                            <label for="post-1"><strong>Link:</strong></label>
                            <br/>
                            <input type="link" className="source-field" name="source" id="post-1" placeholder="Paste your link here" size="100" />
                            <br/>
                            <label for="post-2"><strong>Post Summary:</strong></label>
                            <br/>
                            <textarea className="summary-field" name="summary" id="post-2" placeholder="What's your post about? What's your opinion?" rows="3" cols="104" ></textarea>
                            <br/>
                            <input type="submit" className="create-post-submit" value="Post to your feed"/>
                            <button className="create-post-close" onClick={() => setPost(false)}>Close</button>
                        </fieldset>
                    </form>
                }
            </div>
            <ul className="home-page-rss-feed">
                {feed.map((item, index) => {
                    return (
                        <li key={index} className="news-feed-post">
                            <div className="post-author">
                                <img className="author-profile-pic" src={ProfilePic} alt=""/>
                                <div className="post-author-details">
                                    <h3>{item.author}</h3>
                                    <h5>{item.pubDate}</h5>
                                </div>
                                <div className="post-votes">
                                    <AiIcons.AiFillLike size={25} color={"green"}/>
                                    {item.postVotes[0].$.upvotes}
                                    <AiIcons.AiFillDislike size={25} color={"darkred"}/>
                                    {item.postVotes[0].$.downvotes}
                                </div>
                            </div>
                            <p className="post-author-description">{item.description}</p>
                            <div className="post-news-preview">
                                <a href={item.link}><img src={item.thumbnail[0].$.url} alt="News pic"/></a>
                                <a href={item.link} className="post-news-title"><h3>{item.title}</h3></a>
                            </div>
                        </li>
                    )
                })}
            </ul>
        </div>
    )
}

export default HomeNewsFeed;
