import React from 'react';
import './Top3Users.css';
import TopUser1 from './ProfileP.png';
import TopUser2 from './ProfileP.png';
import TopUser3 from './ProfileP.png';

function Top3Users() {
    return (
        <div className="top-three-users-highlight">
            <div className="top-three-individual-user">
                <img src={TopUser1} alt="First-placed user pic" className="rank-one-profile-pic" />
                <h2>test</h2>
                <h5>1st</h5>
            </div>
            <div className="top-three-individual-user">
                <img src={TopUser2} alt="Second-placed user pic" className="rank-two-profile-pic"/>
                <h2>ProForecaster</h2>
                <h5>2nd</h5>
            </div>
            <div className="top-three-individual-user">
                <img src={TopUser3} alt="Third-placed user pic" className="rank-three-profile-pic"/>
                <h2>Barack Obama</h2>
                <h5>3rd</h5>
            </div>
        </div>
    )
}

export default Top3Users;
