import React from 'react';
import './Profile.css';
import ProfileRightPane from './ProfileRightPane';
import ProfileStats from './ProfileStats';

function Profile() {
    //State isn't needed unless we switch back from scroll buttons
    //Keeping it here anyways to save me recoding it if we do switch
    // const [rightPane, setRightPane] = useState(<ProfileStats />);
    const rightPane = <ProfileStats />

    // const changeRightPane = (component) => {
    //     setRightPane(component);
    // }

    return (
        <div className="profile">
            <h1>My Profile</h1>
            <div className="main-profile-grid">
                {/* <div className="profile-grid-left">
                    <img className="profile-grid-left-profile-pic" src={FakeProfilePic2} alt="Temporary profile pic"/>
                    <ProfileLeftPane handleClick={setRightPane} />
                </div> */}
                <div className="profile-grid-right">
                    <ProfileRightPane component={rightPane}/>
                </div>
            </div>
        </div>
    )
}

export default Profile;
