import React from 'react';
import './ProfileRightPane.css';
import ProfileSummary from './ProfileSummary';
import ProfileStats from './ProfileStats';
import ProfileDetails from './ProfileDetails';
import Settings from './Settings';
import FakeProfilePic2 from './ProfileP.png';

function ProfileRightPane() {
    return (
        <div className="profile-right-pane">
            <h1>test</h1>
            <div className="profile-right-pane-grid">
                <img className="profile-profile-pic" src={FakeProfilePic2} alt="Temporary profile pic"/>
                <ProfileSummary />
            </div>
            <br/>
            <hr/>
            <ProfileStats />
            <hr />
            <ProfileDetails />
            <hr />
            <Settings />
        </div>
    )
}

export default ProfileRightPane;
