import React from 'react'
// import { Link } from 'react-router-dom';
import './Onboarding.css';
import JoinAMarket from './marketOnboarding.JPG';
import MakeAForecast from './marketForecast.JPG';
import CompleteAQuiz from './quizOnboarding.png';
import SubmitAPost from './postOnboarding.JPG';
import VisitProfile from './profileOnboarding.jpg';

function Onboarding(props) {
    return (
        <div className="conditional-div">
            {props.isComplete && <div className="onboarding-incomplete">
                <div className="onboarding-title-div">
                    <h2 className="onboarding-title">Onboarding</h2>
                    <button className="show-hide-onboarding-btn" onClick={props.handleClick}>{props.buttonText}</button>
                </div>
            </div>}
            {!props.isComplete && <div className="onboarding">
                <div className="onboarding-title-div">
                    <h2 className="onboarding-title">Onboarding</h2>
                    <button className="show-hide-onboarding-btn" onClick={props.handleClick}>{props.buttonText}</button>
                </div>
                <ul className="onboarding-list">
                    <li className="onboarding-list-item" style={{ backgroundColor: "lightgreen" }}>
                        <img className="onboarding-list-item-picture" src={VisitProfile} alt="Question Mark Icon"/>
                        <h4>Visit your profile page</h4>
                        <h5>Reward: 100pts</h5>
                    </li>
                    <li className="onboarding-list-item" style={{ backgroundColor: "lightgreen" }}>
                        <img className="onboarding-list-item-picture" src={JoinAMarket} alt="Question Mark Icon"/>
                        <h4>Join a market</h4>
                        <h5>Reward: 100pts</h5>
                    </li>
                    <li className="onboarding-list-item">
                        <img className="onboarding-list-item-picture" src={SubmitAPost} alt="Question Mark Icon"/>
                        <h4>Submit a post</h4>
                        <h5>Reward: 100pts</h5>
                    </li>
                    <li className="onboarding-list-item">
                        <img className="onboarding-list-item-picture" src={MakeAForecast} alt="Question Mark Icon"/>
                        <h4>Submit a forecast</h4>
                        <h5>Reward: 100pts</h5>
                    </li>
                    <li className="onboarding-list-item">
                        <img className="onboarding-list-item-picture" src={CompleteAQuiz} alt="Question Mark Icon"/>
                        <h4>Complete a Learn Quiz</h4>
                        <h5>Reward: 100pts</h5>
                    </li>
                </ul>
            </div>}
        </div>
    )
}

export default Onboarding;
